import axios, { AxiosInstance, InternalAxiosRequestConfig, AxiosResponse } from 'axios';

const baseURL = process.env.REACT_APP_API_URL || 'http://localhost:8000';

const api: AxiosInstance = axios.create({
  baseURL,
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json',
  },
  // Temporarily disable withCredentials to test if it's a CORS issue
  // withCredentials: true,
});

// Request interceptor
api.interceptors.request.use(
  (config: InternalAxiosRequestConfig) => {
    const token = localStorage.getItem('token');
    
    // Log token presence
    console.log('Token in request interceptor:', token ? 'Present' : 'Missing');
    
    if (token && config.headers) {
      config.headers.Authorization = `Bearer ${token}`;
      console.log('Authorization header set:', config.headers.Authorization);
    }
    
    // Log full request details
    console.log('Request details:', {
      url: `${config.baseURL}${config.url}`,
      method: config.method,
      headers: config.headers,
      data: config.data
    });
    
    return config;
  },
  (error) => {
    console.error('Request interceptor error:', error);
    return Promise.reject(error);
  }
);

// Response interceptor
api.interceptors.response.use(
  (response: AxiosResponse) => {
    // Log successful response
    console.log('Response success:', {
      url: response.config.url,
      status: response.status,
      data: response.data
    });
    return response;
  },
  (error) => {
    // Log detailed error information
    console.error('Response error:', {
      url: error.config?.url,
      status: error.response?.status,
      statusText: error.response?.statusText,
      data: error.response?.data,
      headers: error.response?.headers
    });

    // Handle authentication errors
    if (error.response?.status === 401) {
      console.log('Unauthorized error, logging out...');
      localStorage.removeItem('token');
      window.location.href = '/login';
    }

    // For 403 errors, only log out if it's an auth-related endpoint
    if (error.response?.status === 403) {
      const isAuthEndpoint = error.config?.url?.includes('/auth/');
      if (isAuthEndpoint) {
        console.log('Auth-related 403 error, logging out...');
        localStorage.removeItem('token');
        window.location.href = '/login';
      }
      // For other 403s, just pass the error through
    }

    // Extract error message
    const errorMessage = error.response?.data?.message 
      || error.response?.data?.error 
      || error.response?.statusText
      || error.message 
      || 'An unexpected error occurred';

    return Promise.reject({
      ...error,
      message: errorMessage
    });
  }
);

export default api; 