import React from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { useNavigate } from 'react-router-dom';
import { useMutation, useQuery } from '@tanstack/react-query';
import { z } from 'zod';
import toast from 'react-hot-toast';
import { ArrowLeftIcon } from '@heroicons/react/24/outline';
import api from '../api/axios';

const saleSchema = z.object({
  customerName: z.string().min(1, 'Customer name is required'),
  customerEmail: z.string().email('Invalid email address'),
  items: z.array(z.object({
    productId: z.number(),
    quantity: z.number().min(1, 'Quantity must be at least 1'),
  })).min(1, 'At least one item is required'),
  notes: z.string().optional(),
});

type SaleFormData = z.infer<typeof saleSchema>;

interface Product {
  id: number;
  name: string;
  price: number;
  stock: number;
}

const RecordSale: React.FC = () => {
  const navigate = useNavigate();
  const [selectedProducts, setSelectedProducts] = React.useState<Array<{ id: number; name: string; price: number; quantity: number }>>([]);

  const { data: products, isLoading: isLoadingProducts } = useQuery<Product[]>({
    queryKey: ['products'],
    queryFn: async () => {
      const response = await api.get('/api/products');
      return response.data;
    },
  });

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = useForm<SaleFormData>({
    resolver: zodResolver(saleSchema),
    defaultValues: {
      customerName: '',
      customerEmail: '',
      items: [],
      notes: '',
    },
  });

  const handleProductChange = (productId: number) => {
    const product = products?.find(p => p.id === productId);
    if (product) {
      setSelectedProducts(prev => {
        const existing = prev.find(p => p.id === productId);
        if (existing) {
          return prev.map(p => p.id === productId ? { ...p, quantity: p.quantity + 1 } : p);
        }
        return [...prev, { id: product.id, name: product.name, price: product.price, quantity: 1 }];
      });
    }
  };

  const handleQuantityChange = (productId: number, quantity: number) => {
    setSelectedProducts(prev => {
      if (quantity <= 0) {
        return prev.filter(p => p.id !== productId);
      }
      return prev.map(p => p.id === productId ? { ...p, quantity } : p);
    });
  };

  const calculateTotal = () => {
    return selectedProducts.reduce((total, product) => total + (product.price * product.quantity), 0);
  };

  const createSale = useMutation({
    mutationFn: async (data: SaleFormData) => {
      const response = await api.post('/api/sales', {
        ...data,
        items: selectedProducts.map(p => ({
          productId: p.id,
          quantity: p.quantity,
        })),
        total: calculateTotal(),
      });
      return response.data;
    },
    onSuccess: () => {
      toast.success('Sale recorded successfully');
      navigate('/sales');
    },
    onError: (error: Error) => {
      toast.error('Failed to record sale');
      console.error('Error recording sale:', error);
    },
  });

  const onSubmit = async (data: SaleFormData) => {
    await createSale.mutateAsync(data);
  };

  if (isLoadingProducts) {
    return (
      <div className="flex items-center justify-center h-full">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500"></div>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Record Sale</h1>
          <p className="mt-1 text-sm text-gray-500">
            Record a new sale transaction
          </p>
        </div>
        <button
          onClick={() => navigate('/sales')}
          className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500"
        >
          <ArrowLeftIcon className="-ml-1 mr-2 h-5 w-5" />
          Back to Sales
        </button>
      </div>

      {/* Form */}
      <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
        <div className="bg-white shadow rounded-lg p-6">
          <div className="space-y-6">
            {/* Customer Information */}
            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
              <div>
                <label htmlFor="customerName" className="block text-sm font-medium text-gray-700">
                  Customer Name
                </label>
                <input
                  type="text"
                  id="customerName"
                  {...register('customerName')}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500 sm:text-sm"
                />
                {errors.customerName && (
                  <p className="mt-1 text-sm text-red-600">{errors.customerName.message}</p>
                )}
              </div>

              <div>
                <label htmlFor="customerEmail" className="block text-sm font-medium text-gray-700">
                  Customer Email
                </label>
                <input
                  type="email"
                  id="customerEmail"
                  {...register('customerEmail')}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500 sm:text-sm"
                />
                {errors.customerEmail && (
                  <p className="mt-1 text-sm text-red-600">{errors.customerEmail.message}</p>
                )}
              </div>
            </div>

            {/* Items */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Items
              </label>
              <div className="space-y-4">
                {/* Add Product */}
                <div>
                  <select
                    onChange={(e) => handleProductChange(Number(e.target.value))}
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500 sm:text-sm"
                  >
                    <option value="">Select a product</option>
                    {products?.map((product) => (
                      <option key={product.id} value={product.id}>
                        {product.name} - ${product.price} (Stock: {product.stock})
                      </option>
                    ))}
                  </select>
                </div>

                {/* Selected Products */}
                {selectedProducts.length > 0 && (
                  <div className="border rounded-md divide-y">
                    {selectedProducts.map((product) => (
                      <div key={product.id} className="p-4 flex items-center justify-between">
                        <div>
                          <h4 className="text-sm font-medium text-gray-900">{product.name}</h4>
                          <p className="text-sm text-gray-500">${product.price} each</p>
                        </div>
                        <div className="flex items-center space-x-4">
                          <input
                            type="number"
                            min="1"
                            value={product.quantity}
                            onChange={(e) => handleQuantityChange(product.id, Number(e.target.value))}
                            className="w-20 rounded-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500 sm:text-sm"
                          />
                          <button
                            type="button"
                            onClick={() => handleQuantityChange(product.id, 0)}
                            className="text-red-600 hover:text-red-800"
                          >
                            Remove
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>

            {/* Notes */}
            <div>
              <label htmlFor="notes" className="block text-sm font-medium text-gray-700">
                Notes
              </label>
              <textarea
                id="notes"
                rows={3}
                {...register('notes')}
                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500 sm:text-sm"
              />
            </div>

            {/* Total */}
            <div className="border-t pt-4">
              <div className="flex justify-between items-center">
                <span className="text-lg font-medium text-gray-900">Total</span>
                <span className="text-2xl font-bold text-gray-900">
                  ${calculateTotal().toFixed(2)}
                </span>
              </div>
            </div>
          </div>
        </div>

        {/* Submit Button */}
        <div className="flex justify-end">
          <button
            type="submit"
            disabled={isSubmitting || selectedProducts.length === 0}
            className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isSubmitting ? 'Recording...' : 'Record Sale'}
          </button>
        </div>
      </form>
    </div>
  );
};

export default RecordSale; 