import React, { createContext, useContext, useState, useEffect } from 'react';
import { jwtDecode } from 'jwt-decode';
import api from '../api/axios';

interface User {
  id: number;
  name: string;
  email: string;
  role: string;
}

interface AuthContextType {
  user: User | null;
  token: string | null;
  login: (email: string, password: string) => Promise<void>;
  register: (name: string, email: string, password: string) => Promise<void>;
  logout: () => void;
  isAuthenticated: boolean;
  isLoading: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null);
  const [token, setToken] = useState<string | null>(null);
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  // Initialize auth state from localStorage
  useEffect(() => {
    const initializeAuth = async () => {
      try {
        const storedToken = localStorage.getItem('token');
        console.log('Initializing auth with stored token:', storedToken ? 'Present' : 'Missing');
        
        if (!storedToken) {
          setIsAuthenticated(false);
          setUser(null);
          setIsLoading(false);
          return;
        }

        // Validate token
        const decoded = jwtDecode<{ sub: string; name: string; email: string; role: string; exp: number }>(storedToken);
        console.log('Decoded token:', decoded);
        
        if (decoded.exp * 1000 < Date.now()) {
          console.log('Token expired during initialization');
          logout();
          return;
        }

        setUser({
          id: parseInt(decoded.sub),
          name: decoded.name,
          email: decoded.email,
          role: decoded.role
        });
        setToken(storedToken);
        setIsAuthenticated(true);
      } catch (error) {
        console.error('Error initializing auth:', error);
        logout();
      } finally {
        setIsLoading(false);
      }
    };

    initializeAuth();
  }, []);

  const login = async (email: string, password: string) => {
    try {
      console.log('Attempting login for:', email);
      
      const response = await api.post('/api/auth/login', { 
        email: email.trim().toLowerCase(), 
        password 
      });
      
      console.log('Login response:', response.data);
      
      const { token: newToken, user: userData } = response.data;
      
      if (!newToken || typeof newToken !== 'string') {
        console.error('Invalid token format:', response.data);
        throw new Error('Invalid token received from server');
      }

      // Validate token
      const decoded = jwtDecode<{ sub: string; name: string; email: string; role: string; exp: number }>(newToken);
      console.log('Decoded token:', decoded);
      
      if (decoded.exp * 1000 < Date.now()) {
        console.error('Token is expired:', decoded);
        throw new Error('Token is expired');
      }

      // Store token and update state
      localStorage.setItem('token', newToken);
      setToken(newToken);
      setUser(userData);
      setIsAuthenticated(true);
      
      console.log('Login successful, auth state updated');
    } catch (error: any) {
      console.error('Login error:', {
        message: error.message,
        response: error.response?.data,
        status: error.response?.status
      });
      throw error;
    }
  };

  const register = async (name: string, email: string, password: string) => {
    try {
      console.log('Attempting registration for:', email);
      
      const response = await api.post('/api/auth/register', { 
        name: name.trim(), 
        email: email.trim().toLowerCase(), 
        password 
      });
      
      console.log('Registration response:', response.data);
      
      const { token: newToken, user: userData } = response.data;
      
      if (!newToken || typeof newToken !== 'string') {
        console.error('Invalid token format:', response.data);
        throw new Error('Invalid token received from server');
      }

      // Validate token
      const decoded = jwtDecode<{ sub: string; name: string; email: string; role: string; exp: number }>(newToken);
      console.log('Decoded token:', decoded);
      
      if (decoded.exp * 1000 < Date.now()) {
        console.error('Token is expired:', decoded);
        throw new Error('Token is expired');
      }

      // Store token and update state
      localStorage.setItem('token', newToken);
      setToken(newToken);
      setUser(userData);
      setIsAuthenticated(true);
      
      console.log('Registration successful, auth state updated');
    } catch (error: any) {
      console.error('Registration error:', {
        message: error.message,
        response: error.response?.data,
        status: error.response?.status
      });
      throw error;
    }
  };

  const logout = () => {
    console.log('Logging out...');
    localStorage.removeItem('token');
    setToken(null);
    setUser(null);
    setIsAuthenticated(false);
    console.log('Logout complete');
  };

  return (
    <AuthContext.Provider value={{ user, token, login, register, logout, isAuthenticated, isLoading }}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
} 